import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import { MOUSE_ENTER, MOUSE_LEAVE, MOUSE_CLICK, KEY_DOWN } from './constants';

const getClasses = (currentClass, i, current) => {
	if (i > current)
		return currentClass + ' default-star';
	return currentClass + ' pressed-star';
};

const getStarIcon = (className, isEditing, loopVar, current, flip, componentId, readOnly, dispatch, size) => {
	return (
		<now-icon
			className={getClasses(className, isEditing, loopVar, current)}
			flip={flip}
			size={size}
			icon={loopVar <= current ? 'star-fill' : 'star-outline'}
			component-id={componentId + '_' + loopVar}
			key={loopVar}
			aria-checked={loopVar <= current ? 'true' : 'false'}
			value={loopVar}
			title={loopVar}
			on-mouseenter={() => (readOnly ? '' : dispatch(MOUSE_ENTER, { current: loopVar }))}
			on-click={() => (readOnly ? '' : dispatch(MOUSE_CLICK, { rating: loopVar }))}>
		</now-icon>
	);
};

const getElements = (stars, isEditing, current, componentId, readOnly, dispatch,size) => {
	let elements = [];
	for (let i = stars; i >= 1; i--) {
		elements.push(
			<span>
				{
					getStarIcon('full-star', isEditing, i, current, 'horizontal', componentId, readOnly, dispatch, size)
				}
			</span>
		);
	}
	return elements;
};


const snRating = (state, {dispatch}) => {
	const { properties: { readOnly, stars, ariaLabel,size }, current, componentId, isEditing } = state;
	return (
		<Fragment>
			<div
				on-mouseleave={() => (readOnly ? '' : dispatch(MOUSE_LEAVE))}
				role="slider"
				aria-invalid="false"
				tab-index={readOnly ? '-1': '0'}
				aria-valuemin="0"
				aria-valuemax={stars}
				aria-valuenow={current}
				aria-label={ariaLabel}
				style={{ display: 'inline-block' }}
				on-keydown={readOnly ? '' : (e) => dispatch(KEY_DOWN, { key: e.which })}
				on-focusout={() => (readOnly ? '' : dispatch(MOUSE_LEAVE))}
			>
				{
					getElements(stars, isEditing, current, componentId, readOnly, dispatch, size)
				}
			</div>
		</Fragment>
	);
};

export default snRating;