import { get } from 'lodash';
import { MOUSE_ENTER, MOUSE_LEAVE, MOUSE_CLICK, KEY_DOWN,SUBMIT_RATING} from './constants';
import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED,COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const actionHandlers = {
	[MOUSE_ENTER]: {
		effect({ action, updateState, state }) {
			var obj = {}
			obj.current = get(action, 'payload.current');
			if (!state.isEditing)
				obj.isEditing = true;

			updateState({
				...obj
			});
		},
		stopPropagation: true
	},
	[MOUSE_LEAVE]: {
		effect({ properties, updateState }) {
			updateState({
				current: properties.rating,
				isEditing: false
			});
		},
		stopPropagation: true
	},
	[COMPONENT_BOOTSTRAPPED]: {
		effect({ properties, updateState, updateProperties }) {
			let rating = isNaN(properties.rating + '') ? 0 : parseInt(properties.rating);
			let stars = isNaN(properties.stars + '') ? 0 : parseInt(properties.stars);
			if (rating > stars) rating = stars;
			updateProperties({
				rating: rating,
				stars: stars
			});
			updateState({
				current: rating
			});
		}
	},
	[COMPONENT_PROPERTY_CHANGED]: {
		effect({ properties, updateState, updateProperties }) {
			let rating = isNaN(properties.rating + '') ? 0 : parseInt(properties.rating);
			let stars = isNaN(properties.stars + '') ? 0 : parseInt(properties.stars);
			if (rating > stars) rating = stars;
			updateProperties({
				rating: rating,
				stars: stars
			});
			updateState({
				current: rating
			});
		}
	},
	[KEY_DOWN]: {
		effect({ action, dispatch, state, updateState, updateProperties, properties }) {
			let current = state.current;
			let key = get(action, 'payload.key');
			let stars = properties.stars;
			let inc = 1;
			if (key == 39 || key == 40) {
				if (current + inc <= stars)
					current += inc;
			}
			else if (key == 37 || key == 38) {
				if (current - inc >= 0)
					current -= inc;
			}
			else if (key == 13 && current >= 0 && current <= stars) {
				updateProperties({
					rating: current
				});
				dispatch(SUBMIT_RATING, { rating: current+'' });
			}

			var obj = {}
			obj.current = current
			if (!state.isEditing)
				obj.isEditing = true;
			updateState({
				...obj
			});
		},
		stopPropagation: true
	},
	[MOUSE_CLICK]: {
		effect({action, dispatch, properties, updateProperties}) {
			let rating = get(action, 'payload.rating');
			updateProperties({
				rating: rating
			});
			dispatch(SUBMIT_RATING, { rating: rating+'' });	
		},
		stopPropagation: true
	}
};