import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import { actionHandlers } from './actions';
import style from './snStarRating.scss'
import { t } from 'sn-translate';
export const snStarRating = 'sn-star-rating';

/**
 * sn-star-rating is generic seismic component for star rating and This seismic component allows users to rate the content like article, blog,catalog item, etc.
 * 
 * ```
 * <sn-star-rating rating="3"></sn-star-rating>
 * 
 * <sn-star-rating rating="3" read-only={true}></sn-star-rating>
 * 
 * <sn-star-rating rating="3" size="lg" read-only={true}></sn-star-rating>
 * 
 * ```
 * 
 * @seismicElement sn-star-rating
 * @summary Seismic component that allows users to rate the content like article, blog,catalog item, etc.
 * @uib.label Star rating
 * @uib.icon tile-uib-now-star-rating-1-sm
 * @uib.description Seismic component for star rating
 * @uib.category component
 * @uib.associatedTypes global.core
 * @uib.properties readOnly, rating, size, stars, ariaLabel
 * @uib.actions SN_STAR_RATING#SUBMITTED
 */
createCustomElement(snStarRating, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		current: 0,
		isEditing: false
	},
	properties: {
		/**
		 * determines the read only mode of the component.
		 * @type {boolean}
		 * @uib.label Read-only
		 * @uib.description Display in read only mode
		 */
		readOnly: { default: false },
		/**
		 * determines the default rating in the star rating component
		 * @type {number}
		 * @uib.label Rating
		 * @uib.description Rating to display
		*/
		rating: { default: 0 },
		/**
		 * determines the no of stars in the star rating component
		 * @type {number}
		 * @uib.label Number of stars
		 * @uib.description Number of star icons to display in the component
		*/
		stars: { default: 5 },
		/**
		 * determines the aria label of the star rating component
		 * @type {string}
		 * @uib.label ARIA label
		 * @uib.description ARIA label for the star rating
		*/
		ariaLabel: { default: t('Rate')},
		/**
		 * determines the size of the star rating component
		 * @type {("sm"|"md"|"lg")}
		 * @uib.label Size
		 * @uib.description Sets the star size
		*/
		size: {default:"lg"}
	},
	styles: style,
	actionHandlers: actionHandlers
});